/* ModernPages.css */
/* Global Dark Mode & Modern Accent Theme with Smooth Animations */

:root {
    --bg-primary: #111;           /* Dark background */
    --bg-secondary: #1a1a1a;        /* Dark card/background containers */
    --accent-pink: #ff49db;         /* Vibrant pink accent */
    --accent-blue: #2e91ff;         /* Vibrant blue accent */
    --text-primary: #e0e0e0;        /* Light text color */
    --text-secondary: #aaa;
    --shadow: 0 4px 12px rgba(0, 0, 0, 0.5);
  }
  
  * {
    box-sizing: border-box;
  }
  
  body,
  html {
    margin: 0;
    padding: 0;
    font-family: 'Inter', sans-serif;
    background: var(--bg-primary);
    color: var(--text-primary);
    scroll-behavior: smooth;
  }
  
  /* Page Container */
  .page-container {
    padding: 4rem 2rem;
    max-width: 1200px;
    margin: 0 auto;
    animation: fadeIn 1s ease-out forwards;
  }
  
  /* Section Container */
  .section {
    background: var(--bg-secondary);
    padding: 2rem;
    margin-bottom: 2.5rem;
    border-radius: 12px;
    box-shadow: var(--shadow);
    transition: transform 0.3s ease;
  }
  
  .section:hover {
    transform: translateY(-4px);
  }
  
  /* Headings and Subheadings */
  .section-title {
    font-size: 2.5rem;
    margin-bottom: 1rem;
    color: var(--accent-pink);
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .section-subtitle {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    color: var(--accent-blue);
  }
  
  /* Links and Buttons */
  a {
    color: var(--accent-blue);
    text-decoration: none;
    transition: color 0.3s, transform 0.3s;
  }
  
  a:hover {
    color: var(--accent-pink);
    transform: translateY(-2px);
  }
  
  .hero-button {
    display: inline-block;
    padding: 0.9rem 2.5rem;
    background: linear-gradient(90deg, var(--accent-pink), var(--accent-blue));
    color: var(--bg-primary);
    border-radius: 50px;
    text-decoration: none;
    font-weight: 600;
    transition: transform 0.3s, opacity 0.3s;
  }
  
  .hero-button:hover {
    transform: translateY(-3px);
    opacity: 0.9;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }