/* App.css – FAANG‑style Neon ✧ Glass ✧ Motion Theme */

/* ╔════════════════ 1. CSS Tokens ════════════════╗ */
:root {
  /* Palette */
  --bg: #0b0c10;
  --surface: rgba(23,24,29,0.87);
  --glass: rgba(255,255,255,0.06);
  --border: rgba(255,255,255,0.13);

  --txt: #e5e5e5;
  --muted: #8c8f9a;

  --primary: #e600ff;     /* neon magenta   */
  --secondary: #00c4ff;   /* neon cyan      */
  --shadow: rgba(0,0,0,.9);

  /* Sizing */
  --header-h : 200px;
  --nav-h    : 3.25rem;
  --radius   : 14px;

  /* Motion */
  --dur : .35s;
  --ease: cubic-bezier(.33,.99,.6,1);
}

/* ╔════════════════ 2. Reset ════════════════════╗ */
*,
*::before,
*::after { box-sizing: border-box; margin:0; padding:0; }

html { scroll-behavior:smooth; }
body {
  font-family: 'Inter',sans-serif;
  background: var(--bg);
  color: var(--txt);
  line-height: 1.65;
  min-height: 100vh;
}

/* ╔════════════════ 3. Global Wrappers ═════════╗ */
.app-container { display:flex; flex-direction:column; min-height:100vh; }

.content{
  flex:1;
  width:100%;
  max-width:1280px;
  padding:1.25rem;
  margin: calc(var(--header-h) + var(--nav-h) + .75rem) auto 0;
}

/* ╔════════════════ 4. Card Sections ═══════════╗ */
.section{
  background:var(--surface);
  border:1px solid var(--border);
  backdrop-filter:blur(18px);
  border-radius:var(--radius);
  padding:2.25rem;
  margin-bottom:2.5rem;
  box-shadow:0 12px 32px var(--shadow);
  transition: transform var(--dur) var(--ease),
              box-shadow var(--dur) var(--ease);
}
.section:hover{
  transform:translateY(-8px);
  box-shadow:0 20px 40px var(--shadow);
}
.section h2{
  font-size:2.125rem;
  font-weight:700;
  margin-bottom:1rem;
  color:var(--primary);
  position:relative;
}
.section h2::after{
  content:'';
  position:absolute; bottom:-4px; left:0;
  width:60px; height:3px;
  background:linear-gradient(90deg,var(--primary),var(--secondary));
}

/* ╔════════════════ 5. Header ══════════════════╗ */
.header{
  position:fixed; top:0; left:0; width:100%;
  background:var(--surface);
  backdrop-filter:blur(18px);
  border-bottom:1px solid var(--border);
  box-shadow:0 6px 24px var(--shadow);
  z-index:1000;
  display:flex; flex-direction:column; align-items:center;
  padding:0.75rem 1rem 0;
}

/* Logo / name */
.logo{ text-align:center; }
.avatar{
  width:94px; height:94px;
  border-radius:50%;
  border:3px solid var(--secondary);
  transition:transform var(--dur) var(--ease);
}
.avatar:hover{ transform:scale(1.1); }
.header h1{ font-size:1.9rem; margin:.6rem 0 .25rem; }
.header h2{ font-size:1.05rem; color:var(--muted); }

/* Social */
.profile-links{
  display:flex; gap:1.1rem; margin:.6rem 0;
}
.profile-links a{
  color:var(--muted); font-size:1.35rem;
  transition:color var(--dur) var(--ease),
             transform var(--dur) var(--ease);
}
.profile-links a:hover{
  color:var(--primary);
  transform:translateY(-3px);
}

/* ╔════════════════ 6. Navbar ══════════════════╗ */
.navbar{ width:100%; height:var(--nav-h); display:flex; justify-content:center; }
.menu{
  list-style:none; display:flex; gap:1.75rem;
  align-items:center;
}

/* link styling */
.menu li{ position:relative; }
.menu a{
  color:var(--txt);
  font-weight:600;
  padding:.55rem 1.1rem;
  border-radius:var(--radius);
  transition:background var(--dur) var(--ease),
             color      var(--dur) var(--ease),
             transform  var(--dur) var(--ease);
}
.menu a::after{
  content:''; position:absolute; bottom:-6px; left:50%;
  width:0; height:3px; background:var(--secondary);
  transition:width var(--dur) var(--ease),
             left  var(--dur) var(--ease);
}
.menu a:hover::after,
.menu .active a::after{
  width:100%; left:0;
}
.menu a:hover{ background:var(--glass); transform:translateY(-2px); }

/* ╔════════════════ 7. Footer ══════════════════╗ */
.footer{
  background:var(--surface);
  backdrop-filter:blur(18px);
  border-top:1px solid var(--border);
  text-align:center;
  padding:1.25rem 1rem;
  font-size:.9rem; color:var(--muted);
}

/* ╔════════════════ 8. Links (generic) ═════════╗ */
a{ color:var(--secondary); text-decoration:none; }
a:hover{ color:var(--primary); }

/* ╔════════════════ 9. Mobile Nav  ═════════════╗ */
@media (max-width:780px){
  .header h1{ font-size:1.55rem; }
  .avatar{ width:72px; height:72px; }

  .menu{
    position:fixed; top:var(--header-h); left:0; right:0;
    flex-direction:column;
    background:var(--surface);
    backdrop-filter:blur(18px);
    transform:translateY(-140%);
    transition:transform var(--dur) var(--ease);
    padding:1rem 0 2rem;
  }
  .menu.open{ transform:translateY(0); }

  /* hamburger */
  #hamburger{
    display:block;
    position:absolute;
    right:1.35rem; top:1.35rem;
    font-size:1.5rem; color:var(--txt);
    cursor:pointer;
    z-index:1100;
  }

  /* push content a bit further when nav is open */
  .content{ margin-top:calc(var(--header-h) + var(--nav-h) + 5.5rem); }
}