/* ModernPages.css – page‑level layout that inherits App.css tokens */

.page-container{
  padding:4rem 2rem;
  max-width:1200px;
  margin:0 auto;
  animation:fadeIn 1s var(--ease) forwards;
  color:var(--txt);
}

/* Section ≈ card */
.section{
  background:var(--surface);
  border:1px solid var(--border);
  border-radius:var(--radius);
  backdrop-filter:blur(18px);
  box-shadow:0 10px 30px var(--shadow);
  padding:2rem;
  margin-bottom:3rem;
  transition:
    transform var(--dur) var(--ease),
    box-shadow var(--dur) var(--ease);
}
.section:hover{
  transform:translateY(-6px);
  box-shadow:0 20px 40px var(--shadow);
}

/* Titles & subtitles */
.section-title{
  font-size:2.25rem;
  font-weight:700;
  margin:0 0 1rem;
  color:var(--primary);
  letter-spacing:.5px;
}
.section-subtitle{
  font-size:1.375rem;
  margin:0 0 1.5rem;
  color:var(--secondary);
}

/* Body copy */
.section-content{
  font-size:1.075rem;
  line-height:1.7;
  margin-bottom:2rem;
}

/* Code / pre blocks */
pre{
  background:var(--surface);
  border:1px solid var(--border);
  border-radius:var(--radius);
  padding:1rem 1.25rem;
  overflow-x:auto;
  font-size:.95rem;
}

/* Links */
a{
  color:var(--secondary);
  text-decoration:none;
  transition:color var(--dur) var(--ease), transform var(--dur) var(--ease);
}
a:hover{
  color:var(--primary);
  transform:translateY(-2px);
}

/* Fade‑in keyframes */
@keyframes fadeIn{
  from{opacity:0; transform:translateY(30px);}
  to  {opacity:1; transform:translateY(0);}
}

/* Responsive tweaks */
@media(max-width:768px){
  .page-container{padding:3rem 1.25rem;}
  .section{padding:1.5rem;}
  .section-title{font-size:1.9rem;}
  .section-subtitle{font-size:1.25rem;}
}